*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}
body{
    background: var(--color-bg);
}
h1{font-family: 'Montse';}
a{
    text-decoration: none;
    color: unset;
}
.gradient__bg{
    background: rgb(255,255,255);
background: linear-gradient(90deg, rgba(255,255,255,1) 16%, rgba(213,234,245,1) 100%);
}
/* section margin and padding */
.section-padding{
    padding: 4rem 6rem;
}
.section-margin{
    margin: 4rem 6rem;
}
/*media queries*/
@media screen and (max-width:700px) {
    .section-padding{
        padding: 4rem ;
    }
    .section-margin{
        margin: 4rem ;
    } 
}
@media screen and (max-width:550px) {
    .section-padding{
        padding: 4rem 2rem ;
    }
    .section-margin{
        margin: 4rem 2rem ;
    } 
}