.olva_header {
    position: relative;
    display: flex;
    flex-direction: row;
    
}
.free_arrow-left{
    position: absolute;
    top:6%;
    left:58%;
    width: 90px;;

}
.free_arrow-right{
    position: absolute;
    bottom: 35%;
    left:3%;
    transform: rotate(180deg);
    width: 90px;

}
.fill-img{
    margin-top: 50px;
    margin-left:-150px;
    width: 250px;
    height:250px;
    transform: rotate(45deg);
    background-color: #0272a0;
}

.free_arrow-left img,.free_arrow-right img{
    width:100%;
    height: auto;
}
.olva_header-content{
  /* margin-left: 6rem;
  margin-top: 6rem; */
  margin:6rem 0 0 6rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
}
.olva_header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 80px;
    line-height: 75px;
    letter-spacing: 1px;
    color: #0272a0;
}
.olva_header-content h2{
    font-family: var(--font-family);
    font-weight: 700; 
    font-size: 52px;
    color: #474749;
    letter-spacing: 1px;;
}
.olva_header-content p{
    color: #474749;
    font-family: var(--font-family);
    font-size:15px;
    font-weight: 500;
    margin-top: 1.5rem;
    text-align: justify;
}
.olva_header-content button{
    background-color: #0272a0;
    color: aliceblue;
    width: 150px;
    height:40px;
    border-radius: 30px;
    font-size: 15px;
    font-family: var(--font-family);
    text-transform: uppercase;
    letter-spacing: 2px;
    border: 1px solid #0272a0;
    font-weight: 600;
    cursor: pointer;
    margin-top: 2rem;
    box-shadow:
    0 1px 2px rgba(0,0,0,0.07), 
    0 2px 4px rgba(0,0,0,0.07), 
    0 4px 8px rgba(0,0,0,0.07), 
    0 8px 16px rgba(0,0,0,0.07),
    0 16px 32px rgba(0,0,0,0.07), 
    0 32px 64px rgba(0,0,0,0.07);

}
.olva_header-image{
  
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.olva_header-image img{
    width: 100%;
   height: 100%
}