.olva-about{ font-family: poppins;padding: 50px 0 90px 0;}
.olva-about-container{
    position:relative;
  
    display: flex;
    flex-direction: row;
   
    /* border: 1px solid #000; */
}
.about-overlay{display: block;
     position: absolute;
      width: 514px;
      height:386px;
      top:30px;
      right:-20px;
      
      /* border: 1px solid #000; */
      background-color: #0272a0;
      z-index: -999;}
.about-img img{width:95%}
.about-img{ display: flex;
    align-items: flex-start;
    flex-direction: column;}

.about-content{display: flex;
    justify-content: center;
     flex-direction: column;
      /* align-items: center; */
       width: 100%;
        /* border: 1px solid #000; */
        z-index: 0; 
    padding: 0 60px; }
    .about-content h2{
        font-size: 75px;
        text-align: left!important;
        margin-left: -33px;
    }
.about-content h2 span{color: #fff;}
.about-content .about-desc{display:block;
    border:1px solid #000;
    background-color: #fff;
    padding: 20px 50px;
    box-shadow:
    0 1px 2px rgba(0,0,0,0.07), 
    0 2px 4px rgba(0,0,0,0.07), 
    0 4px 8px rgba(0,0,0,0.07), 
    0 8px 16px rgba(0,0,0,0.07),
    0 16px 32px rgba(0,0,0,0.07), 
    0 32px 64px rgba(0,0,0,0.07);
}
.about-desc p{ color: #474749;
    font-size:18px;
    font-weight: 500;
    margin-top: 1.5rem;
    text-align: justify;}