.features-heading{text-align:center;font-size: 30px;
font-family:montserrat; width: 90%;}
.features-heading h1{ font-weight: 400; text-transform: uppercase;}
.features-heading .h-olva{font-size: 95px;font-weight: 800; color: #0272A0;text-transform: uppercase;}

.olva-features{position: relative;
    display: flex;
    flex-direction: row;
margin-top: -50px;
/* border:1px solid #000; */
padding-bottom: 4em;}



.olva-feature-content{
    width:40em;
    height:30em;
   /* border: 1px solid #0272A0; */
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.olva-features-hex{
  
   /* padding: 5em 6em 5em 6em ; */
    display: flex;
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.7));
    /* border:1px solid #000; */
    font-family: montserrat;
    font-weight:200;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    /* flex-wrap: wrap; */
    width: 500px;
    height: 500px;
   
}

.feature-row{display:flex;}
.feature-1{position: relative;margin:0 0 0 1em;
  
   
}
.feature-2{position:relative;margin:0 0 0 1em;}
 .second{margin:-2.7em 0 0 0;}
 .first{margin-right:6.5em;}
.feature-3{position:relative;margin:0 0 0 1em;}
.feature-4{position:relative;margin:0 0 0 1em;}
.card-back{
    width: 11.83em;
    height: 13.52em;
    background-color: #87868b;
    box-sizing: border-box;
    clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0% 75%);
    transition: all 0.4s;
}
.card-front{
    width: 11.83em;
    height: 13.52em;
    text-align: center;
    background-color: #f8f5f5;
    box-sizing: border-box;
    clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0% 75%);
    transition: all 0.4s;
    margin-top: -36vh;
    padding: 10px 1px;
}
.card-front:hover{
    margin-top:-34vh;
    background: linear-gradient(174deg, rgba(255,255,255,1) 26%, rgba(148,187,233,1) 100%);
    margin-left:-1.3vh;
}
.card-desc{
  margin-top:30px;
}
.desc {
    margin-top:10px;
   
}
.desc h2{ font-weight:400;}
.card-icon .i-s{
    font-size:25px;
    color: #0272A0;
}
.olva-feature-content{
   padding: 2em 5em;
    font-family:montserrat;
    
   
}
.olva-feature-content h2{
    color: #353536;
    font-size: 40px;
    font-weight: 800;
    line-height: 45px;
   
}
.olva-feature-content h3{
    font-size:20px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 45px;
    color: #0272A0;
}
.olva-feature-content p{
    color: #474749;
    font-size:15px;
    font-weight: 500;
    margin-top: 1.5rem;
    text-align: justify;
}