.our-products{ box-sizing: border-box;
   }
.product-heading{text-align: center;background-color: #fff; font-family: montserrat;}
.product-heading h1{font-size: 45px; font-weight: 300; text-transform: uppercase; }

.product-heading .h-olva{font-size: 75px; color: #0272a0; font-weight: 800;}
.products{background-color: #fff;
  font-family: "Montserrat", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;}

.grid {
  display: flex;
  width: 100%;
 justify-content: center;
 flex-wrap: nowrap;
}

.grid-item {
    width:330px;
    height:360px;
    margin: 0 20px;
    text-align: center;
  background-color: #fff;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 3rem 6rem rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: 0.2s;
  border: 1px solid #0272a0;
  /* border-right: 2px solid  #0272a0;
  border-left:2px solid #0272a0; */
}

.grid-item:hover {
  transform: translateY(-5%);
  box-shadow: 0 4rem 8rem rgba(0, 0, 0, 0.5);
}
.c-m{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top:5px;
}
.card-img {
  margin-top: 30px;
  display: block;
  width: 10em;
  height: auto;
  object-fit: cover;
}
.card-img-2{
  margin-top: 5px;;
  display: block;
  width: 4.7em;
  height: auto;
  object-fit: cover;
}
.card-img-3{
  margin-top: 10px;;
  display: block;
  width: 6em;
  height: auto;
  object-fit: cover;
}

.card-content {
  padding: 1rem;
}

.card-header {
  text-align: center;
  font-size: 25px;
  font-family: montserrat;
  font-weight: 500;
  color: #0d0d0d;
  margin-bottom: 1.5rem;
}

.card-text {
  text-align: center;
  font-size:18px;
  letter-spacing: 1px;;
  line-height: 1.7;
  color: #3d3d3d;
  margin-bottom: 1rem;
}

.card-btn {

  width: 100%;
  padding: 0.5rem;
  font-size: 20px;
  text-align: center;
  color: #fff;
  background-color: #0272a0;
  border: none;
  border-radius: 0.4rem;
  transition: 0.2s;
  cursor: pointer;
  letter-spacing: 0.1rem;
  
}

.card-btn span {
  margin-left: 0.5rem;
  transition: 0.2s;
}

.card-btn:hover,
.card-btn:active {
  background: rgb(166,160,160);
  background: linear-gradient(90deg, rgba(166,160,160,1) 0%, rgba(153,148,233,1) 70%);
}

.card-btn:hover span,
.card-btn:active span {
  margin-left: 0.5rem;
}
.product-button{
  display: inline-block;
  width:150px;
  height:40px;
   margin-top: 2em;
   box-shadow:
   0 1px 2px rgba(0,0,0,0.07), 
   0 2px 4px rgba(0,0,0,0.07), 
   0 4px 8px rgba(0,0,0,0.07), 
   0 8px 16px rgba(0,0,0,0.07),
   0 16px 32px rgba(0,0,0,0.07), 
   0 32px 64px rgba(0,0,0,0.07);
   border-radius: 30px;}
.card-content button{
  
  background-color: #0272a0;
  color: aliceblue;
  width: 150px;
  height:40px;
  border-radius: 30px;
  font-size: 15px;
  font-family: var(--font-family);
  text-transform: uppercase;
  letter-spacing: 2px;
  border: 1px solid #0272a0;
  font-weight: 600;
  cursor: pointer;
 
  transition: 0.8s;
  
  
}
.card-content button:hover{background: rgb(166,160,160);
  background: linear-gradient(90deg, rgba(166,160,160,1) 0%, rgba(153,148,233,1) 70%);}


