.olva_navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
}
.olva_navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.olva_navbar-links-logo{
    margin-right: 2rem;
}
 .olva_navbar-links_logo img{
    width: 100px;
    height: auto;
}
.olva_navbar-links_container{
    display: flex;
    flex-direction: row;
}
.olva_navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;

}
.olva_navbar-sign button{
    font-size: 15px;
    color: #000;
    width: 130px;
    height:40px;
    background: transparent;
    border-radius: 30px;
    border: 1px solid #0272A0;
    font-family: 'Montserrat';
    box-shadow:
		0 1px 2px rgba(0,0,0,0.07), 
		0 2px 4px rgba(0,0,0,0.07), 
		0 4px 8px rgba(0,0,0,0.07), 
		0 8px 16px rgba(0,0,0,0.07),
		0 16px 32px rgba(0,0,0,0.07), 
		0 32px 64px rgba(0,0,0,0.07);
        transition: background 0.3s, color 0.3s;
        line-height: 25px;

}
.olva_navbar-sign button:hover{
    background: #0272A0;
    color: #fff;
}
.olva_navbar-links_container p,.olva_navbar-sign p{
    color: #87868b;
    text-transform: capitalize;
    font-size: 17px;
    font-weight: 500;
    margin: 0 1rem;
    font-family: var(--font-family);
    word-spacing: 1px;
    line-height: 25px;
    cursor: pointer;
}
